import React from 'react';
import { NavLink } from 'react-router-dom';
import web from "../src/Image/blend.jpg";

const Card = (props) => {
    return (
        <>

            <div className="col-md-4 col-11 mx-auto">
                <div class="card">
                    <img src={props.imgsrc} className="card-img-top" alt="Beautiful img"/>
                    <div className="card-body">
                        <h5 className="card-title font-weight-bold">{props.title}</h5>
                        <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                        <a href={props.link} target="_blank" className="btn btn-danger">Visit us</a>
                    </div>
                </div>
            </div>

        </>
    );

};
export default Card;